import * as React from "react";
import { Navbar, Container, Nav, Dropdown } from 'react-bootstrap';
import { Link } from 'gatsby';
import { useState } from 'react';
import { StaticImage } from "gatsby-plugin-image";

const Header = () => {
    const [showDropdown] = useState(false);
    // console.log('showDropdown::', showDropdown);

    return (
        <Navbar expand="lg" className="navbar navbar-default navbar-fixed-top p-0">
            <Container>
                <Navbar.Brand className="navbar-brand logo" href="#myPage">
                    <div className='wrap'>
                        <Link href='/'>
                            <StaticImage
                                src="../../styles/assets/images/logo2.png"
                                loading="eager"
                                width={64}
                                quality={95}
                                formats={["auto", "webp", "avif"]}
                                alt=""
                                style={{ marginBottom: `var(--space-3)` }}
                            />
                        </Link>
                    </div>
                </Navbar.Brand>
                <Navbar.Toggle />
                <Navbar.Collapse id="myNavbar">
                    <Nav className='nav navbar-nav ms-auto' as="ul">
                        <Nav.Item as="li">
                            <Dropdown className='service'>
                                <Dropdown.Toggle as="a" variant="success">
                                    SERVICES
                                </Dropdown.Toggle>
                                <Dropdown.Menu show={showDropdown} as="ul" className='mt-2'>
                                    <Dropdown.Divider />
                                    <Dropdown.Item as={'li'}>
                                        <Link to={"mern-stack-development"}>MEAN Stack Development</Link>
                                    </Dropdown.Item>
                                    <Dropdown.Item as={'li'}>
                                        <Link to={"mean-stack-development"}>MERN Stack Development</Link>
                                    </Dropdown.Item>
                                    <Dropdown.Item as={'li'}>
                                        <Link to={"blockchain-development"}>BlockChain Development</Link>
                                    </Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </Nav.Item>
                        <Nav.Item as="li">
                            <Link href="#portfolio">PORTFOLIO</Link>
                        </Nav.Item>
                        <Nav.Item as="li">
                            <Link href="/our-team">OUR TEAM</Link>
                        </Nav.Item>
                        <Nav.Item as="li">
                            <Link href="#memes">CONTACT</Link>
                        </Nav.Item>
                        <Nav.Item as="li">
                            <Link href="/blog">BLOG</Link>
                        </Nav.Item>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
};

export default Header;